<template>
    <div id="profile-container">
        
        <div class="profile-data">

            <div class="profile-view">
                <p class="id-user"><span class="bold">Usuario:</span> {{user_id}}</p>
                <img class="profile_img" src="../assets/profile_default.png" alt="profile_default">
            </div>
            
            <div class="profile-details">
                <div class="profile-info">
                    <p class="profile-text"><span class="bold">Nombre de Usuario: </span> {{username}} </p>
                    <p class="profile-text"><span class="bold">Nombre Completo: </span> {{full_name}} </p>
                    <p class="profile-text"><span class="bold">Correo Electrónico: </span> {{email}} </p>
                </div>
                <div class="profile-actions">
                    <button type="button" class="btn btn-danger" @click="closeSession">Cerrar Sesión</button>
                </div>
            </div>

        </div>

    </div>
</template>

<script>
import axios from 'axios'

export default{
    name: "MyProfile",
    data(){
        return {
            user_id: "",
            username: "",
            full_name: "",
            email: "",
        }
    },
    mounted(){
        this.getIdInfo();
        this.loadDataProfile()
    },
    methods:{
        getIdInfo(){
            const id = localStorage.getItem("user_session");
            if(id != null){
                this.user_id = id;
            }
        },
        closeSession(){
            localStorage.removeItem("user_session");
            location.href = "/";
        },
        loadDataProfile(){

            axios
                .get("/api/user/details/"+this.user_id)
                .then(response =>{
                    this.msg = response.data
                    console.log(this.msg);
                    this.username = this.msg.username;
                    this.full_name = this.msg.first_name;
                    this.email = this.msg.email;
                    //console.log(response.data)
                })
                .catch(error => {
                    console.log(error)
                })

        },
    }
}

</script>


<style scoped>

.profile-container{
    background-color: #F2F5F9;
}

.profile-data{
    display: flex;
    justify-content: flex-start;
}

/*Start- Profile img*/
.profile-view{
    margin-left: 4rem;
    padding-top: 2rem;
    padding-bottom: 8rem;
}

.id-user{
    font-size: 25px;
    text-align: center;
}

.profile_img{
    width: 250px;
}

/*End Profile img*/


/*Start- Profile details*/
.profile-details{
    margin-left: 6rem;
    margin-top: 9rem;
}

.profile-text{
    font-size: 25px;
}

.bold{
    font-weight: bold;
    margin-right: 1rem;
}

.profile-actions{
    margin-top: 3rem;
}

/*End- Profile details*/

</style>